import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
// 
// 1. store  
// 2. state data
// 3. getters:computed
// 4. mutations statemethods
// 5. actions :  mutations
    // * actionsmutationsmutationsstate
const store =new Vuex.Store({
	//
	state:{
		postsList:[],
		cartList:[],
		navs: [
        {
          text: "",
          name: "Home",
          icon: "el-icon-goods"
        },
        {
          text: "",
          name: "Headlines",
          icon: "el-icon-document"
        },
        {
          text: "",
          name: "Community",
          icon: "el-icon-message"
        },
        {
          text: "",
          name: "Cart",
          icon: "el-icon-menu"
        },
        {
          text: "",
          name: "MyPerson",
          icon: "el-icon-setting"
        }
      ]
	},
	//
	mutations:{
		updatePostsList(state,{payload=[],method}){
			if (method==1) {
				this.state.postsList=[];
			}
			for (let i=0;i<payload.length;i++) {
				this.state.postsList.push(payload[i]);
			}
		},
		updateCartList(state,payload=[]){
			this.state.cartList=[];
			for (let i=0;i<payload.length;i++) {
				this.state.cartList.push(payload[i]);
			}
		}
	}
	
});
export default store;
